<template>
  <div class="table-container">
    <ul v-if="agentList.length" class="agent-list">
      <li
        v-for="item in agentList"
        :key="item.combineServiceCode"
        class="agent-list_item"
      >
        <div class="info">
          <div class="info-name">
            <h6>{{ item.combineServiceName }}</h6>
            <p class="info-desc">{{ item.combineServiceTitle }}</p>
          </div>
          <img
            class="info-picture"
            :src="item.imgUrlStr"
            alt=""
            @error="handleImgError"
          />
        </div>
        <div class="button-group">
          <span class="buy-button" @click="handleBuy(item)">立即购买</span>
        </div>
      </li>
    </ul>
    <TableEmpty v-else empty-text="暂无产品"></TableEmpty>
  </div>
</template>

<script setup lang="ts">
import { shallowRef } from "vue";
import { useVueRouter } from "@assets/useHook";
import { handleImgError } from "@/assets/common";
import { agentProductListApi } from "@api/order-management-api";

const { goPage, routeQuery } = useVueRouter();

const agentList = shallowRef<any[]>([]);
// 获取代理产品
getAgentProductList();

async function getAgentProductList() {
  try {
    const { data } = await agentProductListApi.getData();
    agentList.value = agentProductListApi.handleData(
      data,
      routeQuery.value.businessType
    );
  } catch (e) {
    console.log(e);
  }
}

console.log(routeQuery);

function handleBuy(info: Record<any, unknown>) {
  goPage(
    `/orderManagement/createOrder/productDetail?businessType=${routeQuery.value.businessType}&companyId=${routeQuery.value.companyId}&profileId=${routeQuery.value.profileId}&calculateType=${info.calculateType}&serviceCode=${info.combineServiceCode}`
  );
}
</script>

<style scoped lang="scss">
.table-container {
  height: calc(100vh - 270px);
  margin-top: 30px;
  background-color: transparent;
  overflow-y: auto;
}

.agent-list {
  @include flex();
  flex-wrap: wrap;

  &_item {
    flex-basis: calc(50% - 20px);
    margin: 20px 10px 0;
    border-radius: 8px;
    background-color: #ffffff;
    overflow: hidden;

    &:nth-child(1),
    &:nth-child(2) {
      margin-top: 0;
    }
  }

  .info {
    @include flex(center, space-between);
    padding: 20px;

    &-name {
      padding-right: 10px;

      h6 {
        color: #242934;
        font-size: 20px;
      }

      p {
        color: #242934;
        font-size: 14px;
        margin-top: 5px;
        @include lineClamp(1);
      }
    }

    &-picture {
      width: 80px;
      height: 80px;
    }
  }

  .button-group {
    @include flex(center, flex-start);
    padding: 15px 20px;
    border-top: 1px solid #edf1f6;

    .buy-button {
      @include flex(center, center);
      font-size: 14px;
      color: #0056ff;
      cursor: pointer;
      user-select: none;

      &::after {
        content: "";
        width: 6px;
        height: 6px;
        margin-left: 2px;
        border-top: 1px solid #0056ff;
        border-right: 1px solid #0056ff;
        transform: rotateZ(45deg);
      }
    }
  }
}
</style>
